(function() {
  app.behaviors.pageLike = [
    {
      ready: function() {
        return this.domHost.pageReady(this);
      },
      locateParentNode: function(el, nodeName) {
        while (el.nodeName !== nodeName) {
          el = el.parentNode;
        }
        return el;
      },
      addBalanceTapped: function(e) {
        var amount;
        amount = e.target.getAttribute('data-amount') || '100';
        return this.domHost.showModalInput("Enter amount in BDT", amount, (function(_this) {
          return function(answer) {
            if (answer) {
              amount = parseInt(answer);
              if (amount < 10) {
                _this.domHost.showModalDialog("Insert an amount of 10 or more.");
                return;
              }
              return _this._addFunds(amount);
            }
          };
        })(this));
      },
      _addFunds: function(amount) {
        var query;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey,
          amountToAdd: amount,
          currency: 'BDT',
          notes: 'Funding for "My Tests"'
        };
        return this.domHost.callApi('/bdemr-wallet-add-funds', query, (function(_this) {
          return function(err, response) {
            if ((!err) && (!response.hasError)) {
              return window.location = response.data.redirectionUrl.replace('//', 'https://');
            }
          };
        })(this));
      },
      _showWalletFundingDialog: function() {
        var params;
        params = this.domHost.getPageParams();
        if ('funding' in params) {
          if (params.funding === 'successful') {
            return this.domHost.showModalDialog("Thank you for your payment.");
          } else {
            return this.domHost.showModalDialog("Something went wrong with your payment. Please try again.");
          }
        }
      },
      _loadWallet: function() {
        var query;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey
        };
        return this.domHost.callApi('/bdemr-wallet-get-balance-and-transaction-details', query, (function(_this) {
          return function(err, response) {
            console.log(response);
            if ((!err) && (!response.hasError)) {
              _this.walletBalance = response.data.balance;
            } else {
              _this.walletBalance = -1;
            }
            return _this.domHost.set('walletBalance', _this.walletBalance);
          };
        })(this));
      },
      _loadPatientWallet: function(id, cbfn) {
        var query;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey,
          userIdOverride: id
        };
        return this.domHost.callApi('/bdemr-wallet-get-balance-and-transaction-details', query, (function(_this) {
          return function(err, response) {
            console.log(response);
            if ((!err) && (!response.hasError)) {
              _this.patientWalletBalance = response.data.balance;
            } else {
              _this.patientWalletBalance = -1;
            }
            _this.domHost.set('patientWalletBalance', _this.patientWalletBalance);
            if (cbfn) {
              return cbfn();
            }
          };
        })(this));
      },
      _loadPatientOrganizationWallet: function(organizationId, patientId, cbfn) {
        var query;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey,
          organizationId: organizationId,
          searchString: ''
        };
        return this.domHost.callApi('/bdemr-organization-list-patient', query, (function(_this) {
          return function(err, response) {
            var i, len, patient, ref;
            if (!response.data) {
              return cbfn(null);
            }
            ref = response.data.matchingPatientList;
            for (i = 0, len = ref.length; i < len; i++) {
              patient = ref[i];
              if (patient.patientId === patientId) {
                cbfn(patient);
                return;
              }
            }
            return cbfn(null);
          };
        })(this));
      },
      _chargePatient: function(patientId, amount, notes, cbfn) {
        var query;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey,
          amountInBdt: amount,
          notes: notes,
          userIdOverride: patientId
        };
        return this.domHost.callApi('/bdemr-wallet-charge-user', query, (function(_this) {
          return function(err, response) {
            if (err) {
              return cbfn(err);
            }
            if (response.hasError) {
              return cbfn(response.error);
            }
            return cbfn();
          };
        })(this));
      },
      _chargeOrganizationPatient: function(arg, cbfn) {
        var amount, context, notes, organizationId, patientId, query;
        patientId = arg.patientId, amount = arg.amount, notes = arg.notes, context = arg.context, organizationId = arg.organizationId;
        query = {
          apiKey: (app.db.find('user'))[0].apiKey,
          amountInBdt: amount,
          notes: notes,
          context: context,
          organizationId: organizationId,
          userIdOverride: patientId
        };
        return this.domHost.callApi('/bdemr-wallet-charge-user', query, (function(_this) {
          return function(err, response) {
            if (response.hasError) {
              return cbfn(response.error);
            }
            return cbfn();
          };
        })(this));
      }
    }, app.behaviors.debug
  ];

}).call(this);
